.complete{
  display: flex;
  flex-flow: wrap;
  width: 80%;
  margin: auto;
}
@media (max-width : 1000px) {
  .complete{
      padding-inline: unset;
  }
}
.modal-popup{
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
}